import React, { Component } from 'react';
// import PropTypes from 'prop-types';

import { Button } from 'reactstrap';
import ReactSlickCarousel from 'components/ReactSlickCarousel/ReactSlickCarousel';

import Image from 'components/Image';
import SegmentHeader from '../SegmentHeader';

import styles from './Portfolio.module.scss';

const data = [
  'XrD8ZqsZkHU',
  '71U66I9OKP4',
  '0heT3Yd17tk',
  'igoJiLsKNOY',
  'dltXxW5Y0Eo',
  // 'https://www.youtube.com/embed/oKkexeqhip4',
  // 'https://www.youtube.com/embed/hUJ_3vZhZK0',
]

class Portfolio extends Component {
  constructor(props) {
    super(props);

    this.state = {
      clickedArray: [],
    };
  }

  render() {
    const { clickedArray } = this.state;

    return (
      <div id="portfolio" className={styles.productionProcess}>
        <SegmentHeader
          title="OUR WORK"
          subTitle="PORTFOLIO"
        />
        <ReactSlickCarousel
          lazyLoad
          // autoplay
          arrows
          slidesToShow={3}
          className="white-sliders-padded"
          responsive={[{
            breakpoint: 992,
            settings: {
              slidesToShow: 1,
            },
          }, {
            breakpoint: 1400,
            settings: {
              slidesToShow: 2,
            },
          }]}
        >
          {
            data.map((d, index) => (
              <div className={styles.videoContainer} key={`portfolio-video-${index}`}>
                <div className={styles.videoInner}>
                  {
                    clickedArray[index] ? (
                      <iframe title="Infinite Frontier Creative - Animated Video Portfolio" height="360" width="480" src={`https://www.youtube.com/embed/${d}`} />
                    ) : (
                        <Button
                          color="link"
                          onClick={() => {
                            const newArray = [...clickedArray];
                            newArray[index] = true;
                            this.setState({
                              clickedArray: newArray,
                            });
                          }}
                        >
                          <Image src={`https://img.youtube.com/vi/${d}/hqdefault.jpg`} alt="Portfolio Video" />
                          <i className="fab fa-youtube" />
                        </Button>
                      )
                  }
                </div>
              </div>
            ))
          }
        </ReactSlickCarousel>
      </div>
    );
  }
}

Portfolio.defaultProps = {
};

Portfolio.propTypes = {
};

export default Portfolio;
